@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body, .container, .choices, .both-choices, .points {
  justify-content: center;
  align-items: center;
  display: flex;
}

body {
  color: #fff;
  height: 100vh;
  background-color: #000;
}

.container {
  flex-direction: column;
  gap: 20px;
}

.choices {
  width: 400px;
  gap: 20px;
}

.choices img {
  width: 100px;
  height: 100px;
  cursor: pointer;
  border-radius: 50px;
}

.choices img:active {
  transform: scale(.95);
}

.both-choices h3:first-child {
  margin-right: 20px;
}

.both-choices span {
  color: #ff0;
  font-weight: 500;
}

.result {
  color: #ff0;
  font-size: 28px;
}

.points h3:first-child {
  margin-right: 15px;
}

.points span {
  color: #ff0;
  font-weight: 500;
}

